/* 全局样式 - 黑色背景 + 淡蓝色主题 */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', 'Arial', sans-serif;
  background: #000000;
  color: #ffffff;
  min-height: 100vh;
  line-height: 1.6;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* 头部样式 */
.app-header {
  background: rgba(15, 15, 15, 0.95);
  padding: 2rem;
  text-align: center;
  border-bottom: 1px solid #66b2ff;
  backdrop-filter: blur(10px);
}

.app-header h1 {
  color: #66b2ff;
  margin-bottom: 0.5rem;
  font-size: 2.5rem;
  font-weight: 700;
  letter-spacing: 0.5px;
}

.app-header p {
  color: #cccccc;
  font-size: 1.1rem;
}

/* 主内容区 */
.app-main {
  flex: 1;
  padding: 2rem;
  max-width: 1000px;
  margin: 0 auto;
  width: 100%;
}

/* 导航样式 */
.app-nav {
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
  gap: 1rem;
  border-bottom: 1px solid #1a1a1a;
  padding-bottom: 1rem;
}

.app-nav button {
  padding: 1rem 2rem;
  border: none;
  border-radius: 8px;
  background: #1a1a1a;
  color: #cccccc;
  cursor: pointer;
  transition: all 0.3s;
  font-size: 1rem;
  font-weight: 500;
}

.app-nav button.active {
  background: #66b2ff;
  color: #000000;
}

.app-nav button:hover {
  background: #3399ff;
  color: #000000;
}

/* 内容卡片样式 */
.tab-content {
  background: #0a0a0a;
  padding: 2rem;
  border-radius: 16px;
  border: 1px solid #1a1a1a;
  box-shadow: 0 8px 32px rgba(102, 178, 255, 0.1);
}

/* 钱包连接组件样式 */
.wallet-connect {
  background: #0a0a0a;
  border: 1px solid #1a1a1a;
  border-radius: 16px;
  padding: 2rem;
  text-align: center;
  margin-bottom: 2rem;
}

.wallet-connect h2 {
  color: #66b2ff;
  font-size: 1.5rem;
  margin-bottom: 1rem;
  font-weight: 600;
}

.wallet-connect p {
  color: #cccccc;
  margin-bottom: 1.5rem;
}

.connect-button {
  background: linear-gradient(135deg, #66b2ff 0%, #3399ff 100%);
  color: #000000;
  border: none;
  padding: 1rem 2rem;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s;
  width: 100%;
}

.connect-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 4px 20px rgba(102, 178, 255, 0.3);
}

.connect-button:disabled {
  background: #333333;
  color: #666666;
  cursor: not-allowed;
}

/* 钱包信息样式 */
.wallet-info {
  background: #0a0a0a;
  border: 1px solid #1a1a1a;
  border-radius: 16px;
  padding: 2rem;
  margin-bottom: 2rem;
}

.wallet-info h3 {
  color: #66b2ff;
  font-size: 1.3rem;
  margin-bottom: 1.5rem;
  font-weight: 600;
}

.wallet-info p {
  color: #cccccc;
  margin: 0.5rem 0;
}

.wallet-address {
  background: rgba(102, 178, 255, 0.1);
  padding: 0.75rem 1rem;
  border-radius: 8px;
  border: 3px solid #66b2ff;
  color: #66b2ff;
  font-family: 'Monaco', 'Menlo', monospace;
  font-size: 0.9rem;
  margin: 0.5rem 0;
  font-weight: 500;
}

.network-status {
  padding: 0.5rem 1rem;
  border-radius: 8px;
  font-size: 0.9rem;
  font-weight: 500;
  margin-top: 1rem;
  display: inline-block;
}

.network-status.success {
  background: rgba(102, 178, 255, 0.1);
  color: #66b2ff;
  border: 3px solid #66b2ff;
}

.network-status.error {
  background: rgba(255, 102, 102, 0.1);
  color: #ff6666;
  border: 1px solid #ff6666;
}

/* 转账表单样式 */
.transfer-section h2 {
  color: #66b2ff;
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
  font-weight: 600;
}

.transfer-form {
  /* max-width: 500px; */
  margin: 0 auto;
}

.input-group {
  margin-bottom: 1.5rem;
}

.input-group label {
  display: block;
  color: #cccccc;
  margin-bottom: 0.5rem;
  font-weight: 500;
}

.input-group input {
  width: 100%;
  padding: 1rem;
  background: #1a1a1a;
  border: 1px solid #333333;
  border-radius: 10px;
  color: #ffffff;
  font-size: 1rem;
}

.input-group input:focus {
  outline: none;
  border-color: #66b2ff;
  box-shadow: 0 0 0 2px rgba(102, 178, 255, 0.2);
}

.transfer-button {
  background: linear-gradient(135deg, #66b2ff 0%, #3399ff 100%);
  color: #000000;
  border: none;
  padding: 1rem 2rem;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s;
  width: 100%;
  margin: 1rem 0;
}

.transfer-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 4px 20px rgba(102, 178, 255, 0.3);
}

.transfer-button:disabled {
  background: #333333;
  color: #666666;
  cursor: not-allowed;
}

/* 邀请面板样式 */
.referral-dashboard h2 {
  color: #66b2ff;
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
  font-weight: 600;
}

.invite-section {
  background: #1a1a1a;
  padding: 1.5rem;
  border-radius: 12px;
  margin-bottom: 1.5rem;
  border: 1px solid #333333;
}

.invite-section h3 {
  color: #66b2ff;
  margin-bottom: 1rem;
  font-weight: 600;
}

.invite-link-container {
  display: flex;
  gap: 1rem;
  margin: 1rem 0;
}

.invite-link-input {
  flex: 1;
  padding: 0.75rem;
  background: #0a0a0a;
  border: 1px solid #333333;
  border-radius: 8px;
  color: #cccccc;
  font-size: 0.9rem;
}

.copy-button {
  background: #66b2ff;
  color: #000000;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s;
}

.copy-button:hover {
  background: #3399ff;
}

.invite-description {
  color: #cccccc;
  font-size: 0.9rem;
  margin-top: 1rem;
}

/* 统计卡片样式 */
.referral-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
  margin: 1.5rem 0;
}

.stat-card {
  background: #1a1a1a;
  padding: 1.5rem;
  border-radius: 12px;
  border: 1px solid #333333;
  text-align: center;
}

.stat-card h4 {
  color: #66b2ff;
  margin-bottom: 0.5rem;
  font-weight: 600;
}

.stat-card p {
  color: #ffffff;
  font-size: 1.2rem;
  font-weight: 700;
}

/* 底部样式 */
.app-footer {
  background: #0a0a0a;
  color: #cccccc;
  text-align: center;
  padding: 1.5rem;
  margin-top: auto;
  border-top: 1px solid #1a1a1a;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .app-main {
    padding: 1rem;
  }
  
  .app-nav {
    flex-direction: column;
  }
  
  .tab-content {
    padding: 1.5rem;
  }
  
  .referral-stats {
    grid-template-columns: 1fr;
  }
  
  .invite-link-container {
    flex-direction: column;
  }
}

.loading {
  text-align: center;
  padding: 2rem;
  color: #66b2ff;
  font-weight: 600;
}

.referral-notice {
  background: rgba(102, 178, 255, 0.1);
  border: 1px solid #66b2ff;
  border-radius: 12px;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
}

.referral-notice h3 {
  color: #66b2ff;
  margin-bottom: 0.5rem;
  font-weight: 600;
}

.referral-notice p {
  color: #cccccc;
  margin: 0.25rem 0;
}

.allowance-info {
  background: rgba(102, 178, 255, 0.1);
  padding: 1rem;
  border-radius: 8px;
  border: 1px solid #66b2ff;
  margin-bottom: 1.5rem;
}

.allowance-info p {
  color: #66b2ff;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.approve-button {
  background: #66b2ff;
  color: #000000;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s;
}

.approve-button:hover:not(:disabled) {
  background: #3399ff;
}

.approve-button:disabled {
  background: #333333;
  color: #666666;
  cursor: not-allowed;
}

/* Header样式 - 左侧Logo布局 */
.app-header {
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a1a 100%);
  padding: 1.5rem 2rem;
  border-bottom: 1px solid #434a52;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1.5rem;
  backdrop-filter: blur(10px);
}

.header-logo {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.logo-icon {
  width: 40px;
  height: 40px;
  background: linear-gradient(135deg, #66b2ff 0%, #3399ff 100%);
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 18px;
  color: #000000;
}

.logo-text {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.app-header h1 {
  color: #66b2ff;
  font-size: 1.8rem;
  font-weight: 700;
  margin: 0;
  letter-spacing: 0.5px;
}

.app-header p {
  color: #cccccc;
  font-size: 1rem;
  margin: 0;
  font-weight: 400;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .app-header {
    padding: 1rem 1.5rem;
    gap: 1rem;
  }
  
  .logo-icon {
    width: 32px;
    height: 32px;
    font-size: 14px;
  }
  
  .app-header h1 {
    font-size: 1.4rem;
  }
  
  .app-header p {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .app-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.75rem;
  }
  
  .header-logo {
    gap: 0.75rem;
  }
  
  .app-header h1 {
    font-size: 1.2rem;
  }
  
  .app-header p {
    font-size: 0.85rem;
  }
}

/* 钱包地址行样式 */
.wallet-address-row {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  margin: 1rem 0;
}

.address-section {
  flex: 1;
}

.network-section {
  flex-shrink: 0;
}

.wallet-address {
  background: rgba(102, 178, 255, 0.1);
  padding: 0.75rem 1rem;
  border-radius: 8px;
  border: 1px solid #66b2ff;
  color: #66b2ff;
  font-family: 'Monaco', 'Menlo', monospace;
  font-size: 0.9rem;
  margin: 0.5rem 0;
  font-weight: 500;
  min-width: 150px;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .wallet-address-row {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }
  
  .address-section,
  .network-section {
    width: 100%;
  }
  
  .wallet-address {
    min-width: auto;
  }
}

/* 钱包已连接标题美化 */
.wallet-info h3 {
  color: #66b2ff;
  font-size: 1.3rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  position: relative;
  padding-left: 2rem;
  display: flex;
  align-items: center;
}

.wallet-info h3::before {
  content: '';
  position: absolute;
  left: 0;
  width: 1.5rem;
  height: 1.5rem;
  background: linear-gradient(135deg, #66b2ff 0%, #3399ff 100%);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.wallet-info h3::after {
  content: '';
  position: absolute;
  left: 0.45rem;
  width: 0.6rem;
  height: 0.3rem;
  border-left: 2px solid #000000;
  border-bottom: 2px solid #000000;
  transform: rotate(-45deg);
  z-index: 1;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .wallet-info h3 {
    font-size: 1.1rem;
    padding-left: 1.8rem;
  }
  
  .wallet-info h3::before {
    width: 1.3rem;
    height: 1.3rem;
  }
  
  .wallet-info h3::after {
    left: 0.4rem;
    width: 0.5rem;
    height: 0.25rem;
  }
}

/* USDT转账标题居中 */
.transfer-section h2 {
  color: #66b2ff;
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  text-align: center;
  position: relative;
  padding-bottom: 0.5rem;
}

.transfer-section h2::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background: linear-gradient(135deg, #66b2ff 0%, #3399ff 100%);
  border-radius: 2px;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .transfer-section h2 {
    font-size: 1.3rem;
  }
  
  .transfer-section h2::after {
    width: 40px;
    height: 2px;
  }
}

/* 项目信息区域样式 */
.project-info-section {
  margin-top: 2rem;
}

.project-info-section .transfer-form {
  background: #0a0a0a;
  padding: 2rem;
  border-radius: 16px;
  border: 1px solid #1a1a1a;
  box-shadow: 0 8px 32px rgba(102, 178, 255, 0.1);
}

.project-info-section h2 {
  color: #66b2ff;
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  text-align: center;
}

.project-info-content {
  margin-top: 1.5rem;
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
}

.info-card {
  background: #1a1a1a;
  padding: 1.5rem;
  border-radius: 12px;
  border: 1px solid #333333;
  transition: all 0.3s ease;
}

.info-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(102, 178, 255, 0.15);
  border-color: #66b2ff;
}

.info-card h3 {
  color: #66b2ff;
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 1rem;
  border-bottom: 1px solid #333333;
  padding-bottom: 0.5rem;
}

.info-body {
  color: #cccccc;
}

.info-item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
  padding: 0.3rem 0;
}

.info-item .label {
  color: #999999;
  font-weight: 500;
}

.info-item .value {
  color: #66b2ff;
  font-weight: 600;
}

.step {
  display: flex;
  align-items: center;
  margin-bottom: 0.8rem;
  padding: 0.5rem 0;
}

.step-number {
  background: #66b2ff;
  color: #000000;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-size: 0.8rem;
  margin-right: 0.8rem;
  flex-shrink: 0;
}

.step-text {
  color: #cccccc;
}

.warning-item {
  margin-bottom: 0.5rem;
  padding: 0.3rem 0;
  color: #ff6666;
}

.warning-item span {
  color: #ff6666;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .project-info-section .transfer-form {
    padding: 1.5rem;
  }
  
  .info-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .info-card {
    padding: 1.2rem;
  }
}

@media (max-width: 480px) {
  .project-info-section .transfer-form {
    padding: 1rem;
  }
  
  .info-card {
    padding: 1rem;
  }
  
  .info-item {
    flex-direction: column;
    gap: 0.2rem;
  }
  
  .step {
    align-items: flex-start;
  }
  
  .step-text {
    font-size: 0.9rem;
  }
}

/* Logo样式美化 */
.header-logo {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.logo-image {
  width: 50px;
  height: 50px;
  border-radius: 12px;
  object-fit: cover;
  border: 2px solid #66b2ff;
  box-shadow: 0 4px 12px rgba(102, 178, 255, 0.3);
  transition: all 0.3s ease;
}

.logo-image:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 20px rgba(102, 178, 255, 0.4);
}

.logo-text {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.logo-text h1 {
  color: #66b2ff;
  font-size: 1.8rem;
  font-weight: 700;
  margin: 0;
  letter-spacing: 0.5px;
  text-shadow: 0 2px 4px rgba(102, 178, 255, 0.2);
}

.logo-text p {
  color: #cccccc;
  font-size: 1rem;
  margin: 0;
  font-weight: 400;
  letter-spacing: 0.3px;
}

/* 如果没有图片，使用文字Logo的样式 */
.logo-placeholder {
  width: 50px;
  height: 50px;
  background: linear-gradient(135deg, #66b2ff 0%, #3399ff 100%);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 20px;
  color: #000000;
  border: 2px solid #66b2ff;
  box-shadow: 0 4px 12px rgba(102, 178, 255, 0.3);
  transition: all 0.3s ease;
}

.logo-placeholder:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 20px rgba(102, 178, 255, 0.4);
}

/* 响应式设计 */
@media (max-width: 768px) {
  .logo-image,
  .logo-placeholder {
    width: 40px;
    height: 40px;
  }
  
  .logo-text h1 {
    font-size: 1.4rem;
  }
  
  .logo-text p {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .header-logo {
    gap: 0.75rem;
  }
  
  .logo-image,
  .logo-placeholder {
    width: 35px;
    height: 35px;
    font-size: 16px;
  }
  
  .logo-text h1 {
    font-size: 1.2rem;
  }
  
  .logo-text p {
    font-size: 0.8rem;
  }
}

/* 固定金额输入框样式 */
.fixed-amount-input {
  width: 100%;
  padding: 1rem;
  background: #1a1a1a;
  border: 1px solid #66b2ff;
  border-radius: 10px;
  color: #66b2ff;
  font-size: 1.1rem;
  font-weight: 600;
  text-align: center;
  cursor: not-allowed;
}

.amount-note {
  text-align: center;
  margin-top: 0.5rem;
}

.amount-note p {
  color: #66b2ff;
  font-size: 0.9rem;
  font-weight: 500;
  margin: 0;
}

/* 转账按钮文字 */
.transfer-button {
  /* 其他样式保持不变 */
  font-weight: 600;
}